/* Accordeon */

.date__item {
    list-style-type: none;
    display: flex;
    cursor: pointer;
    padding-top: 60px;
    padding-bottom: 12px;
    width: 100%;
    text-align: left;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    transition: 0.4s;  
}

.date__item::after {
    display: flex;
    justify-content: center;
    content: '\2304'; /* Unicode  */
    font-size: 13px;
   
    float: right;
    margin-left: 20px;
    margin-top: 22px;
   
}
.active::after {
    content: '\2303';
}

.card-news__box {
    display: none;
}
.card-news__box.active {
    list-style-type: none;
    padding-top: 40px;
    display: block;
    overflow: hidden;
}

